header {
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  border-bottom: 2px solid #000;
  display: flex;
  align-items: center;
}
header nav {
  width: 100%;
  height: inherit;
  display: flex;
  align-items: center;
}
header nav .logo-image__wrapper {
  display: flex;
  align-items: center;
  border-right: 2px solid #000;
  height: inherit;
}
header nav .logo-image__wrapper .logo-image {
  width: 120px;
  margin-right: 100px;
}
header nav .navbar {
  display: flex;
  margin-left: 100px;
  justify-content: space-between;
}
header nav .navbar .menu {
  display: flex;
}
header nav .navbar .menu li {
  padding: 0 30px;
  border-right: 2px solid #000;
}
header nav .navbar .menu li:nth-of-type(4) {
  border-right: none;
}
header nav .navbar .utility {
  display: flex;
}